.menu {
  background: #fafafa;
  min-width: 150px;
  border: 1px solid #ddd;
  position: absolute;
}
.menu li {
  padding: 6px 10px;
  text-align: left;
  font-size: 14px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}
.menu li:last-child {
  border-bottom: none;
}
.menu li:hover {
  background: #f0f0f0;
}



