.detail-panel {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.4);
}
.detail-panel .content {
  min-width: 110px;
  min-height: 110px;
  max-height: 92%;
  max-width: 98%;
  object-fit: contain;
  display: block;
  margin-bottom: 15px;
  background-color: #797979;
  box-sizing: border-box;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.detail-panel .content p {
  margin: 10px;
}
.detail-panel .name {
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border-radius: 10px;
  padding: 5px 15px;
}
.detail-panel .close-btn {
  position: absolute;
  right: -20px;
  top: -20px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 40px;
  background-color: rgba(0, 0, 0, 0.5);
  border: none;
  width: 80px;
  height: 80px;
  border-radius: 40px;
  cursor: pointer;
  box-sizing: border-box;
  padding-right: 11px;
  padding-top: 9px;
  outline: none;
}
.detail-panel .close-btn:hover {
  color: white;
  background: rgba(0, 0, 0, 0.3);
}
.detail-panel .close-btn:active {
  color: #fafafa;
  background: rgba(0, 0, 0, 0.4);
}



