.files-panel {
  position: relative;
  display: block;
  margin: 10px;
  min-height: 300px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #fcfcfc;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}
.files-panel .main-header {
  text-align: left;
  background: #f3f3f3;
  padding: 6px;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}
.files-panel .main-header button {
  margin: 0;
  margin-right: 10px;
}
.files-panel .main-header .current-path {
  padding: 2px 10px;
  display: inline-block;
  min-width: 60px;
  background: rgba(0, 0, 0, 0.1);
}
.files-panel .files-holder {
  position: relative;
  padding-bottom: 200px;
}
.files-panel .files-holder .message-holder {
  position: absolute;
  background: rgba(197, 197, 197, 0.8);
  padding: 5px;
  left: 50%;
  color: #565656;
  margin: 2px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  transform: translateX(-50%);
  border-radius: 1px;
}
.files-panel .files-holder .message-holder .error {
  color: #B12B00;
}
.file-panel-item {
  box-sizing: border-box;
  cursor: pointer;
  padding: 10px;
  width: 140px;
  height: 140px;
  float: left;
  text-align: center;
  overflow: hidden;
}
.file-panel-item .name {
  margin: 0;
  word-break: break-all;
  text-overflow: ellipsis;
  width: 100%;
}
.file-panel-item .icon {
  display: flex;
  height: 93px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.file-panel-item .preview {
  box-sizing: border-box;
}
.file-panel-item .file-name-input {
  transform: translateY(-3px);
  font-size: 16px;
  outline: none;
  width: 100%;
  padding: 0;
  text-align: center;
  word-break: break-word;
  display: inline-block;
  height: 40px;
}
.file-panel-item:hover {
  background: #f5f5f5;
}
.file-panel-item:active {
  background: #e0e0e0;
}
.dir-item .preview {
  height: 57px;
  width: 70px;
  padding: 5px;
  background-color: #ECAF53;
  border: 1px solid #a0a0a0;
}
.file-item .preview {
  height: 64px;
  width: 50px;
  border: 1px solid #a0a0a0;
  padding-top: 11px;
  color: #666;
}
.image-item .icon .preview {
  object-fit: contain;
  height: auto;
  padding: 2px;
  max-width: 105px;
  max-height: 79px;
  box-sizing: border-box;
  border: 1px solid #a0a0a0;
}



